<!--授权页面-->
<template>
 <div class="login">
    <p class="text_center name">跳转中。。。</p>
 </div>
</template>

<script> 
import md5 from '@/common/js/md5.js';

    export default {
      data(){
        return{
        /* 
            *用户名 form 表单
        */
        form_user:{
            hotel_code: 'SHJKJD',
            userName: 'wx_cxy',
            passWord: '123456',
        },
        }
      },
      created(){
          this.login_user();
      },
      methods:{
          /* this.$router.push({ path:router,query: params  }) */
        /* 
            根据用户名和密码登录
        */
        login_user(){
            let url = 'https://login.eloadspider.com/v1/common/employee/login',that = this; 

            if( that.form_user.userName.trim().length > 0 && that.form_user.passWord.trim().length > 0 ){
            let params = {
                'code': that.form_user.hotel_code,
                'user_name': that.form_user.userName,
                'password': md5.hexMD5(that.form_user.passWord),
                'shift_id': ''
            };
            that.$axios.post(url,params).then(function(res) { 
                console.log('登录', res.data );
                if(res.data.message == 'success'){
                    that.$router.push({ path:'/index' })
                
                }else{
                that.$message.error('登录失败，失败原因：' + res.data.message);
                } 
            }).catch(error => { 

            });
            }
        },
      }
    }
</script>


<style scoped >
.text_center{
    text-align: center;
}
.login{
    overflow: hidden;
}

</style>



